<template>
    <v-row class="fill-height" no-gutters>
        <v-col>
            <v-card>
                <v-toolbar dark color="primary">
                    <v-btn icon dark @click="$emit('dialog-close')">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-title>PUI Lab Results</v-toolbar-title>
                    <v-spacer></v-spacer>
                </v-toolbar>
                <v-card-text>
                    <v-container>

                        <v-simple-table
                        >
                            <template v-slot:default>
                                <thead>
                                <tr>
                                    <th class="text-left">Date</th>
                                    <th class="text-left">COVID-19 Test Result</th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr v-for="item in lab_results" :key="item.id">
                                    <td>{{ item.date }}</td>
                                    <td>{{ item.covid19test }}</td>
                                </tr>
                                </tbody>
                            </template>
                        </v-simple-table>
                    </v-container>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
    import {getPatientLabResults} from "../../util/api"

    export default {
        name: "PuiLabResultTable",
        props: {
            patient_id: Number
        },
        created() {
            this.loadApi()
        },
        data() {
            return {
                lab_results: []
            }
        },
        methods: {
            loadApi() {
                getPatientLabResults(this.patient_id).then(res => {
                    const {data} = res.data
                    this.lab_results = data
                }).catch(error => {

                })
            }
        }
    }
</script>

